<script>
  import Addtrade from "./Addtrade.svelte";
  import { onMount } from "svelte";
  import { compressToUTF16, decompressFromUTF16 } from "async-lz-string";
  function addProd() {
    setTimeout(() => {
      document.getElementById("add").click();
    }, 100);
  }
  let trades = [];
  function json(response) {
    return response.json();
  }
  async function refreshProd() {
    let uid = localStorage.getItem("uid");
    fetch(`http://15.206.212.248:3000/data/getAll/${uid}`, {
      method: "GET",
    })
      .then(json)
      .then(async function (data) {
        console.log("Request succeeded with JSON response", data);
        trades = data.result;
        const compressed = await compressToUTF16(JSON.stringify(trades));
        localStorage.setItem("trades", compressed);
      })
      .catch(function (error) {
        alert(error);
      });
    console.log("refresh");
  }

  onMount(async () => {
    refreshProd();
  });

  async function handleMessage(event) {
    if (event.detail.text == "added") {
      let storedTradesComp = localStorage.getItem("trades");
      const decompressed = await decompressFromUTF16(storedTradesComp);
      trades = JSON.parse(decompressed);
    }
  }
</script>

<div class="col-12">
  <div class="card">
    <div class="card-header">
      <h3 class="card-title">Trades</h3>
      <div class="ms-auto">
        <!--Refresh-->
        <button
          class="btn btn-outline-warning d-none d-sm-inline-block"
          on:click={refreshProd}
        >
          <!-- Download SVG icon from http://tabler-icons.io/i/plus -->
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="icon icon-tabler icon-tabler-refresh"
            width="44"
            height="44"
            viewBox="0 0 24 24"
            stroke-width="1.5"
            stroke="currentColor"
            fill="none"
            stroke-linecap="round"
            stroke-linejoin="round"
          >
            <path stroke="none" d="M0 0h24v24H0z" fill="none" />
            <path d="M20 11a8.1 8.1 0 0 0 -15.5 -2m-.5 -4v4h4" />
            <path d="M4 13a8.1 8.1 0 0 0 15.5 2m.5 4v-4h-4" />
          </svg>
          Refresh
        </button>
        <button
          data-bs-toggle="modal"
          id="add"
          data-bs-target="#modal-add"
          hidden
        />
        <button
          class="btn btn-outline-warning d-sm-none btn-icon"
          on:click={refreshProd}
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            oyt
            class="icon icon-tabler icon-tabler-refresh"
            width="44"
            height="44"
            viewBox="0 0 24 24"
            stroke-width="1.5"
            stroke="currentColor"
            fill="none"
            stroke-linecap="round"
            stroke-linejoin="round"
          >
            <path stroke="none" d="M0 0h24v24H0z" fill="none" />
            <path d="M20 11a8.1 8.1 0 0 0 -15.5 -2m-.5 -4v4h4" />
            <path d="M4 13a8.1 8.1 0 0 0 15.5 2m.5 4v-4h-4" />
          </svg>
        </button>
        <!--Add -->
        <button
          class="btn btn-outline-primary d-none d-sm-inline-block"
          on:click={addProd}
        >
          <!-- Download SVG icon from http://tabler-icons.io/i/plus -->
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="icon"
            width="24"
            height="24"
            viewBox="0 0 24 24"
            stroke-width="2"
            stroke="currentColor"
            fill="none"
            stroke-linecap="round"
            stroke-linejoin="round"
            ><path stroke="none" d="M0 0h24v24H0z" fill="none" /><line
              x1="12"
              y1="5"
              x2="12"
              y2="19"
            /><line x1="5" y1="12" x2="19" y2="12" /></svg
          >
          Add
        </button>
        <button
          data-bs-toggle="modal"
          id="add"
          data-bs-target="#modal-add"
          hidden
        />
        <button
          class="btn btn-outline-primary d-sm-none btn-icon"
          on:click={addProd}
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="icon"
            width="24"
            height="24"
            viewBox="0 0 24 24"
            stroke-width="2"
            stroke="currentColor"
            fill="none"
            stroke-linecap="round"
            stroke-linejoin="round"
            ><path stroke="none" d="M0 0h24v24H0z" fill="none" /><line
              x1="12"
              y1="5"
              x2="12"
              y2="19"
            /><line x1="5" y1="12" x2="19" y2="12" /></svg
          >
        </button>
      </div>
    </div>
    <div class="table-responsive mb-1">
      <table class="table card-table table-vcenter text-nowrap datatable">
        <thead>
          <tr>
            <th class="w-1">Scrip</th>
            <th>Contract Open Date</th>
            <th>Contract Expiration</th>
            <th>Lots</th>
